body {
	font-family: SF Pro Display;
}

.loading {
	text-align: center;
}

.social-url {
	color: white;
	text-decoration: none;
}

.tech-quiz {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	margin-top: 2%;
	margin-left: auto;
	margin-right: auto;
	width: fit-content;
	white-space: pre-wrap;
	overflow-wrap: break-word;
	font-family: SF Pro Display;
	font-style: normal;
	font-weight: 300;
	font-size: 18px;
	line-height: 1.2;
	text-align: left;
	margin-bottom: 5vh;
}

.tech2-quiz,
.management-quiz,
.design-quiz {
	display: flex;
	flex-direction: column;
	justify-content: center;
	margin: 2% 0;
	white-space: pre-wrap;
	overflow-wrap: break-word;
	font-family: SF Pro Display;
	font-style: normal;
	font-weight: 300;
	font-size: 18px;
	line-height: 1.2;
	text-align: center;
	margin-bottom: 5vh;
}

.question-count {
	text-align: center;
	font-size: 24px;
}

.question-text {
	display: flex;
	flex-direction: column;
	justify-content: center;
	margin: 2% 0;
	white-space: pre;
	overflow: auto;
}
.blackButton {
	cursor: pointer;
	/* width:60%; */
	width: 100%;
	background: transparent;
	outline: none;
	border: 1px solid grey;
	border-radius: 15px;
	/* height:45px; */
	margin-bottom: 10px;
	color: white;
	text-align: left;
	padding: 10px 15px;
	font-size: 1rem;
	transition: all 250ms ease-in-out;
}
/* .blackButton:first-of-type{
  margin-top: 10px;
} */
.blackButton:hover {
	background: dodgerblue;
}
.blackButton:focus {
	background: dodgerblue;
}
.blackButton:active {
	background: dodgerblue;
}
.blackButton:target {
	background: dodgerblue;
}
.blackButton:visited {
	background: dodgerblue;
}
