.btn-bottom {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	margin-top: 20px;
	margin-bottom: 15px;
}
.btn-bottom > button {
	flex: 1;
	margin: 0 3vw;
	background: transparent;
	outline: none;
	border: 1px solid dodgerblue;
	/* height:30px; */
	color: dodgerblue;
	height: 40px;
	font-size: 20px;
	border-radius: 5px;
	cursor: pointer;
}
.btn-bottom > button:hover {
	background: dodgerblue;
	color: white;
}
.btn-bottom > button:active {
	transform: scale(0.9);
}
.progress {
	background-color: #5d5d5d;
	border-radius: 20px;
	margin: 15px 0;
	height: 8px;
	width: 250px;
}
.time-p {
	margin: 0;
	padding: 5px 0 0;
	margin-bottom: 15px;
}
.progress-done {
	background: #018cf1;
	border-radius: 20px;
	color: #fff;
	height: 100%;
	/* width: 80%; */
	transition: 1s ease 0.3s;
}
.progress-done-red {
	background: red;
	border-radius: 20px;
	color: red;
	height: 100%;
	/* width: 80%; */
	transition: 1s ease 0.3s;
}
.timer {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	margin-bottom: 5vh;
}
.mgmt-answer {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	/* resize: none; */
	/* width:60%; */
	width: 90%;
	height: 20vh;
	background: transparent;
	outline: none;
	border: 1px solid grey;
	border-radius: 15px;
	/* height:45px; */
	margin-bottom: 10px;
	margin: auto;
	color: white;
	text-align: left;
	padding: 10px 15px;
	font-size: 1rem;
	transition: all 250ms ease-in-out;
	resize: none;
}
#disabled-btn {
	cursor: not-allowed;
}
#disabled-btn:hover {
	background-color: inherit;
	color: dodgerblue;
}
