.modal {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.75);
}
.modal-main {
	text-align: center;
	z-index: 25;
	position: fixed;
	/*width: 50%;*/
	height: auto;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	border-radius: 15px;
	box-sizing: border-box;
	padding: 30px 30px 15px 30px;
}
.confirm {
	padding-bottom: 30px;
	width: 90%;
}
.submit-heading {
	margin-top: 0;
}
.modal-contact {
