.close {
	float: left;
	width: 20px;
	height: 20px;
	opacity: 1;
	cursor: pointer;
}
.close:hover {
	opacity: 1;
}
.close:before,
.close:after {
	position: absolute;
	left: 25px;
	top: 10px;
	content: " ";
	height: 25px;
	width: 2px;
	background-color: white;
}
.close:before {
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
}
.close:after {
	-webkit-transform: rotate(-45deg);
	        transform: rotate(-45deg);
}
.modal-heading {
	color: white;
	font-size: 2em;
	-webkit-margin-before: 0em;
	        margin-block-start: 0em;
}
.sub-heading-modal {
	color: white;
}
.modal-main-contact {
	color: white;
	background: #191919 /*#312f31*/;
	/* max-width: 700px; */
}
.modal-main-contact a {
	color: #027aff;
}
.sub-heading-modal {
	font-family: SF Pro Display;
	font-style: normal;
	font-weight: 300;
	font-size: 5.2vw;
	line-height: 1.2;
	text-align: center;
	margin-bottom: 5vh;
}
@media screen and (max-width: 350px) {
	.close:before,
	.close:after {
		height: 20px;
	}
}

