.landing_page {
	height: 100vh;
	width: 100%;
	display: flex;
	justify-content: center;
}

.landing_page .left {
	margin-top: 50px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 45vw;
}

.landing_page .right {
	height: 100vh;
	width: 55vw;
}

.landing_page .right img {
	height: 100vh;
	object-fit: cover;
	width: 55vw;
