#profile-container {
	color: white;
	text-align: center;
	height: 65px;
	width: 65px;
	overflow: hidden;
	margin: 0 auto;
	border-radius: 50%;

	background-color: #191919;
	color: #ffffff;
	opacity: 0.8;
	-webkit-backdrop-filter: blur(20px);
	backdrop-filter: blur(20px);

	transition: all 0.3s ease-in-out;
}
#profile-container:hover {
	z-index: 1000;
	border-radius: 10px;
	padding: 10px 20px;
	height: 150px;
	width: max-content;
	/*min-width: max-content;*/
	transition: all 0.3s ease-in-out;
	margin-bottom: -105px;
}
#profile-container:hover img {
	padding-right: 5px;
	transition: all 0.3s ease-in-out;
}
#profile-container #profile-wrapper img {
	position: relative;
	z-index: 20;
	border-radius: 50%;
	display: block;
	height: 55px;
	width: 55px;
	object-fit: cover;
	margin: 5px auto;
	/*transition: all 0.3s ease;*/
}
